    <div class="SummaryPage">
        <MainPageHeader/>
        <div v-if="tab" class="content">
            <header class="main-header">
                <p>Lorem ips</p>
                <h1>{{tab.name}}</h1>
            </header>
            <div class="divider" />
            <div class="text-header">
                <h1>Lorem ipsum sed.</h1>
                <p>Lorem ipsum sed. Lorem</p>
                <h2>Lorem ipsum sed. Lorem ipsum sed.Lorem ipsum sed.</h2>
            </div>
            <section class="section section-1">
                <header>
                    <p>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                        Sed nec dignissim 
                    </p>
                    <div class="image-placeholder" />
                </header>
                <div class="info">
                    <PolicyRow :checked="areAllChecked" @input="acceptAllInput">
                        Akceptuję wszystkie poniższe zgody
                    </PolicyRow>
                    <PolicyRow class="inset" :checked="policyStates[0]" @input="policyInput(0, $event)">
                        Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                        Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,
