                        a type specimen book. It has survived not only five centuries,
                        but also the leap into electronic typesetting, remaining essentially unchanged.
                        It was popularised in the 1960s with the release of Letraset sheets containing
                    </PolicyRow>
                    <PolicyRow class="inset" :checked="policyStates[1]" @input="policyInput(1, $event)">
                        Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                        Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,
                        when an unknown printer took a galley of
                    </PolicyRow>
                    <PolicyRow class="inset" :checked="policyStates[2]" @input="policyInput(2, $event)">
                        Lorem Ipsum is simply dummy text of the printing and typesetting
                    </PolicyRow>
                </div>
            </section>
        </div>
        <TabFooter v-if="tab" :tab="tab" :buttonEnabled="areAllChecked" @buttonClicked="proceed" />
        <PageNotFound v-if="loadError" :message="loadError" />
        <ContentLoader v-if="!tab" />
    </div>
</template>

<script>
    import MainPageHeader from '../components/MainPageHeader.vue';
    import PageNotFound from '../components/PageNotFound.vue';
    import ContentLoader from '../components/ContentLoader.vue';
    import TabFooter from '../components/TabFooter.vue';
    import PolicyRow from '../components/PolicyRow.vue';
    import { getTabService } from '../services/tabService';
    import { getAuthService } from '../services/authService';

    export default {
        name: 'SummaryPage',
        components: {
            MainPageHeader,
            PageNotFound,
            ContentLoader,
            TabFooter,
            PolicyRow,
        },

        data() {
            return {
                tab: null,
                loadError: null,
                policyStates: [ false, false, false ],
                loggedInEmail: null,
            };
        },

        mounted() {
            (async () => {
                const [
                    tab,
                    loggedInEmail,
                ] = await Promise.all([
                    getTabService().getTab(this.$route.params.tabKey),
                    getAuthService().getLoggedInEmail(),
                ]);

                this.loggedInEmail = loggedInEmail;
                if (this.loggedInEmail == null) {
                    this.$router.replace({ name: 'logInPage', params: { tabKey: this.$route.params.tabKey } });
                    return;
                }

                this.tab = tab;

                if (this.tab === null) {
                    this.loadError = `The tab with the specified key doesn't exist`;
                }
            })();
        },

        methods: {
            acceptAllInput() {
                if (this.policyStates.some(p => p == false)) {
                    this.policyStates = this.policyStates.map(p => true);
                } else {
                    this.policyStates = this.policyStates.map(p => false);
                }
            },

            policyInput(index, state) {
                const states = this.policyStates.slice();
                states[index] = !this.policyStates[index];
                this.policyStates = states;
            },

            proceed() {
                if (!this.areAllChecked) {
                    return;
                }

                if (this.loggedInEmail != null) {
                    this.$router.push({name: 'proper', params: { 'tabKey': this.tab.key } });
                } else {
                    this.$router.push({ name: 'logInPage', params: { 'tabKey': this.tab.key } });
                }
            },
        },

        computed: {
            areAllChecked() {
                return !this.policyStates.some(p => p == false);
            },
        },
    };
</script>

